<template>
  <div class="wrap-g">
    <header>
      <img src="../../../static/img/home/logo.png" style="width: 30px;height: 30px;float:left;;padding: 5px;box-shadow: 5px 0px 5px -5px rgba(0,0,0,0.50);">
      <img src="../../../static/img/home/fenlei.png" style="width: 20px;height: 20px;padding: 10px;float: right;box-shadow: -5px 0px 5px -5px rgba(0,0,0,0.50);">

      <el-tabs v-model="activeName" @tab-click="handleClick" style="font-size: 12px" class="setcourses">
        <el-tab-pane label="精选课程" name="/selectedcourses"></el-tab-pane>
        <el-tab-pane label="最新上线" name="/newcourses"></el-tab-pane>
        <el-tab-pane label="即将上线" name="/sooncourses"></el-tab-pane>
      </el-tabs>
    </header>
    <div class="main" style="margin-top: 40px;">
      <transition name="fade" mode="in-out">
        <router-view ></router-view>
      </transition>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        activeName:"/home",
      }
    },
    methods:{
      handleClick(e){
        this.$router.push({ path: e.name});
        console.log(e.name)
      },

    },
    mounted(){
      this.activeName=this.$route.path;
    },
  }
</script>


<style lang="less">
.setcourses{
  .el-tabs__nav{
    width: 100%;
    text-align: center;
  }
  .el-tabs__active-bar{
    display: none;
  }
}

</style>



